import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { Row, } from 'antd';
import MenuTable from './MenuTable'
import MenuDialog from './MenuDialog'

@inject('SystemStore')
@observer
class SystemMenu extends Component {
    render() {
        const { modalVisible,  } = this.props.SystemStore
        return <div className="tab-content-bg">
            <Row>
                {/*<Button type='primary' style={{ marginLeft: 10 }} onClick={e => modalChange(0)}>新增</Button>*/}
            </Row>
            <MenuTable />
            {modalVisible && <MenuDialog />}
        </div>;
    }
    componentWillUnmount() {
        this.props.SystemStore.clearData()
    }
}

export default SystemMenu;